/* open-sans-regular - latin */
@font-face {
     font-family: 'Open Sans';
     font-style: normal;
     font-weight: 400;
     src: url('open-sans-29-latin/open-sans-v29-latin-regular.eot'); /* IE9 Compat Modes */
     src: local(''),
          url('open-sans-29-latin/open-sans-v29-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
          url('open-sans-29-latin/open-sans-v29-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
          url('open-sans-29-latin/open-sans-v29-latin-regular.woff') format('woff'), /* Modern Browsers */
          url('open-sans-29-latin/open-sans-v29-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
          url('open-sans-29-latin/open-sans-v29-latin-regular.svg#OpenSans') format('svg'); /* Legacy iOS */
   }
   /* open-sans-700 - latin */
   @font-face {
     font-family: 'Open Sans';
     font-style: normal;
     font-weight: 700;
     src: url('open-sans-29-latin/open-sans-v29-latin-700.eot'); /* IE9 Compat Modes */
