          url('open-sans-29-latin/open-sans-v29-latin-700.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
          url('open-sans-29-latin/open-sans-v29-latin-700.woff2') format('woff2'), /* Super Modern Browsers */
          url('open-sans-29-latin/open-sans-v29-latin-700.woff') format('woff'), /* Modern Browsers */
          url('open-sans-29-latin/open-sans-v29-latin-700.ttf') format('truetype'), /* Safari, Android, iOS */
          url('open-sans-29-latin/open-sans-v29-latin-700.svg#OpenSans') format('svg'); /* Legacy iOS */
   }